.match_card_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: #4ecca3;
  width: 28vw;
  margin-bottom: 300px;
  height: 45vh;
  border-radius: 25px;
  border-color: #eeeeee;
  border-style: inset;
  border-width: 10px;
}

.title_card {
  display: flex;
  background-color: #232931;
  font-size: 20px;
  font-weight: 600;
  color: #eeeeee;
  width: 100%;
  justify-content: center;
  margin-top: 20px;
}

.body_text {
  text-align: center;
  margin: 10px;
  margin-top: 20px;
}

.card_footer {
  justify-content: center;
  align-items: center;
}